import {useSelector} from "react-redux";
import {useHistory} from "react-router-dom";
import './CurrentBook.scss';
import empty_book_thumbnail from "../media/book_thumbnail.png"


export const CurrentBook = () => {

    const history = useHistory()
    const book_etag = useSelector(state => state.currentBookReducer.book_etag)
    const books = useSelector(state => state.allBooksReducer.books)
    let key_counter = 0

    const handlerBack = () => {
        history.goBack()
    }

    let arr_bookcards = books.map( item => {
        let thumbnail_url = item.volumeInfo.imageLinks !== undefined
            ? item.volumeInfo.imageLinks.thumbnail
            : empty_book_thumbnail
        let categories = item.volumeInfo.categories !== undefined
            ? item.volumeInfo.categories
            : ""
        let authors = item.volumeInfo.authors ? item.volumeInfo.authors.join(', ') : item.volumeInfo.authors
        let buy_button = item.saleInfo.buyLink !== undefined ? <a href={item.saleInfo.buyLink} rel="noreferrer" target="_blank" className="buy_button shadow mb-3 rounded">Приобрести в Google Play</a> : ""
        let page_count = item.volumeInfo.pageCount !== undefined ? <div className="d-flex justify-content-center">Количество страниц:&nbsp;<strong>{item.volumeInfo.pageCount}</strong></div> : ""
        let sale_info = item.saleInfo.listPrice !== undefined ? <div className="d-flex justify-content-center">Стоимость:&nbsp;<strong>{item.saleInfo.listPrice.amount}</strong>&nbsp;{item.saleInfo.listPrice.currencyCode}</div> : ""
        let published_date = item.volumeInfo.publishedDate !== undefined ? <div className="d-flex justify-content-center">Дата публикации:&nbsp;{item.volumeInfo.publishedDate}</div> : ""
        let publisher = item.volumeInfo.publisher !== undefined ? <div className="d-flex justify-content-center mb-2">{item.volumeInfo.publisher}</div> : ""
        if (item.etag === book_etag) {
            return (
                <div className="container pt-5" key={key_counter++}>
                    <div className="row">
                        <div className="col-sm-6">
                            <div className="row mb-3 d-flex justify-content-center">
                                <img src={thumbnail_url} className="current_card_img rounded shadow" alt="Book thumbnail"/>
                            </div>
                            <div className="row mb-3 ">
                                {published_date}
                                {publisher}
                                {page_count}
                                {sale_info}
                            </div>
                        </div>
                        <div className="col-sm-6">
                            <h3>{item.volumeInfo.title}</h3>
                            <h5 className="text-muted">{authors}</h5>
                            <p><u>{categories}</u></p>
                            <p className="description mb-4">{item.volumeInfo.description}</p>
                            {buy_button}
                            <div className="back_button shadow mb-5 rounded" onClick={handlerBack}>Назад</div>
                        </div>
                    </div>
                </div>
            )
        }
        return null
    })

        return (
            <div>
                {arr_bookcards}
            </div>)

}