const defaultState = {
    large_thumbnail: "",
    book_etag: "",
}

export const currentBookReducer = (state= defaultState, action)=> {
    switch (action.type) {
        case "BOOK_ETAG":
            return {...state, book_etag: action.payload}
        case "LARGE_THUMBNAIL":
            return {...state, large_thumbnail: action.payload}
        default: return state
    }
}